///
// Footer Meta
// @group components
///


.footer-meta {
    position: relative;
    line-height: $line-height-perfect-fifth;

    .footer-meta__card {
        .footer-meta__card__img {
            img {
                width: 100%;
                margin: $measure-small 0;
            }
        }
    }
}

.footer-meta {
    position: relative;
    &:before { 
        @include absolute(top); 
        @include non-selectable; 
        content: ""; 
        width: 200vw; 
        height: 100%; 
        pointer-events: none;
 
        @include media("below:medium") { 
            max-width: calc(100% + #{$measure-medium}); 
        } 
    } 
 
    &.section--undergraduate { 
        &:before { 
            background-color: $color-flamingo; 
        } 
    } 
 
    &.section--executive-education { 
        &:before { 
            background-color: $color-vivid; 
        } 
    } 
 
    &.section--masters { 
        &:before { 
            background-color: $color-prussian; 
        } 
    } 
 
    &.section--mba { 
        &:before { 
            background-color: $color-sherpa; 
        } 
    } 
 
    &.section--phd, 
    &.section--executive-phd { 
        margin-top: $measure-enormous; 
 
        &:before { 
            background-color: $color-allports; 
        } 
    } 
}

$footer-themes: (
        corporate: (
            $color-concrete        
        ),
        undergraduate: (
            $color-flamingo
        ),
        masters: (
            $color-prussian
        ),
        phd: (
            $color-allports
        ),
        mba: (
            $color-sherpa
        ),
        executive-education: (
            $color-vivid
        )
    );

    @each $theme, $colors in $footer-themes {
        $bg-color:   nth($colors, 1);
    
        .scp--#{$theme} { 
            .footer-meta {
                &.section--scp-theme {
                    &:before {
                        background-color: $bg-color;
                    }
                }
            }
        }
    }
        
.footer-meta__card {
    @include media("below:small") {
        margin-bottom: $measure-medium;
    }
    @include grid-column(6, small);

    h5 {
        font-weight: $font-weight-book;
        text-transform: uppercase;
        color: $color-lucifer;
    }

    a {
        vertical-align: top;
    }
}

.footer-meta__card__wrapper {    
    padding: $measure-medium;
    box-shadow: 0 0 66px 3px rgba(0,0,0,.15);
    background-color: $color-white;
    height: 100%;

    @include media("below:small") {
        width: auto;
    }
    @include media("above:small") {
        width: 100%;
    }   
}

.footer-meta__card__social {
    margin-top: $measure-medium;

    a {
        @include anchor(inline-block);
        margin-right: $measure-small; 
    }

    .footer-meta__card__social__icons {
        display: inline;
        font-size: $font-size-delta;
    }
}
