table {
  overflow: hidden;
  position: relative;
  z-index: 0;
  width: auto;
  max-width: 100%;
  margin: $measure-small auto;
  border-collapse: collapse;
  
  td,
  th {
    padding: 10px;
    position: relative;
    outline: 0;
    text-align: center;
    border-bottom: 1px solid $color-whisper;
    vertical-align: top;
    border-left: 0;
    border-right: 0;
    margin: 0;
  }
  
  th {
    font-weight: bold;
  }

  tr {
    position: relative;
    border-left: 0;
    border-right: 0;
    margin: 0;
    padding: 0;
  }
  
  thead th {
    border-bottom-width: 2px;
  }
  
  tbody th {
    text-align: left;
    white-space: nowrap;
  }
  tbody > tr:hover {
    td,
    th {
      background: $color-white-smoke;
    }
  }
  
  td:hover,
  thead th:not(:empty):hover {
    &:after {
      content: '';
      position: absolute;
      z-index: -2;
      top: -5000px;
      left: 0;
      width: 100%;
      height: 10000px;
      background: $color-white-smoke;
    }
  }
  
  tbody > tr > td:hover {
    background: $color-gainsboro;
  }
}
